import React, {useState, useRef, useEffect} from 'react';
import FirstSliderWrapper from '../../SliderViews/FirstSliderWrapper';
import Slide from '../../SliderViews/Slide';
import LastSliderWrapper from '../../SliderViews/LastSliderWrapper';
import Wrapper from '../../SliderViews/Wrapper';
import {Swiper, SwiperSlide} from 'swiper/react';
// import { Player, BigPlayButton, Video } from 'video-react';
import SwiperCore, {Navigation, Pagination, Controller, Thumbs} from 'swiper';
import 'swiper/scss';
import 'swiper/scss/navigation';
import style from '../../SliderViews/styles/index.module.scss';

import AsyncComponent from '../../../helpers/asyncComponent';

const Player = AsyncComponent(() => import('../../VideoView/ViewsVideo'));


SwiperCore.use([Navigation, Pagination, Controller, Thumbs]);

const FancyButton = React.forwardRef(({className, ...props}, ref) => (
  <div {...props} ref={ref} className={className}>
    {props.children}
  </div>
));

/**
 *
 * @param {
 * imageOrVideoSet - массив изображений
 * defaultImage - изображение по умолчанию
 * defaultThumb - превью по умолчанию
 *
 * } param0
 * @returns
 */

const PreviewSliderContainer = ({
  isLoadingDataProduct,
  imageOrVideoSet = [],
  defaultImage,
  defaultThumb,
  isPreview
}) => {
  const [thumbsSwiper, setThumbsSwiper] = useState(null);

  const [controlledSwiper, setControlledSwiper] = useState(null);
  const [controlledTwoSwiper, setControlledTwoSwiper] = useState(null);

  const navigationPrevRef = useRef(null);
  const navigationNextRef = useRef(null);
console.log({isLoadingDataProduct})
  return (
    <Wrapper
      isPreview={isPreview}
    >
      {
        !isPreview ?
          <FirstSliderWrapper>

            <FancyButton className={'swiper-button-prev'} ref={navigationPrevRef}></FancyButton>

            <Swiper
              onSwiper={setControlledTwoSwiper}
              controller={{control: controlledSwiper}}
              navigation={{
                prevEl: navigationPrevRef.current,
                nextEl: navigationNextRef.current
              }}
              direction={'vertical'}
              spaceBetween={10}
              resizeObserver
              resistance
              lazyPreloadPrevNext={1}
              observer={true}
              observeSlideChildren
              slidesPerView={3}
              className={style['swipper__prev-container--vertical']}
            >
              {
                imageOrVideoSet?.length ? imageOrVideoSet.map((el, index) => {
                  if (el.type === 'video') {
                    return <SwiperSlide
                      key={index}
                      className={style['swipper__prev-slide--vertical']}
                    >
                      <Player
                        className="news-details-page__slider_item"
                        preLoad={"auto"}
                        // video={el.preview}
                        preview={el.preview}

                      />
                    </SwiperSlide>
                  } else {
                    return <SwiperSlide
                      className={style['swipper__prev-slide--vertical']}
                      key={index}
                    >
                      <Slide
                        image={!!el.image_thumb ? el.image_thumb : defaultImage}
                      />
                    </SwiperSlide>
                  }
                }) : (defaultImage ? (
                  <SwiperSlide className={style['swipper__prev-slide--vertical']}>
                    <Slide image={defaultImage}></Slide>
                  </SwiperSlide>
                ) : null)
              }
            </Swiper>

            <FancyButton className={'swiper-button-next'} ref={navigationNextRef}></FancyButton>
          </FirstSliderWrapper>
          : null
      }

      <LastSliderWrapper>
        <Swiper
          spaceBetween={10}
          slidesPerView={1}
          lazyPreloadPrevNext={1}
          direction={'horizontal'}
          onSwiper={setControlledSwiper}
          controller={{control: controlledTwoSwiper}}
          className={style['first-slider__gallery--horizontal']}
        >
          {
            imageOrVideoSet?.length ? 
              imageOrVideoSet.map((el, index) => {
                if (el.type === 'video') {
                  return (
                    <SwiperSlide
                      key={index}
                      className={style['swipper__slide--h-100']}
                    >
                      <Player
                        className={style["news-details-page__slider_item"]}
                        controls={false}
                        width={'100%'}
                        height={'100%'}
                        muted={true}
                        autoPlay={true}
                        preLoad={"auto"}
                        loop={true}
                        fluid={false}
                        video={el.preview}
                        preview={el.preview}
                      />
                    </SwiperSlide>
                  );
              } else {
                return (
                  <SwiperSlide
                    key={index}
                    className={style['swipper__slide--h-100']}
                  >
                    <Slide image={isLoadingDataProduct? defaultImage : el.image}/>
                  </SwiperSlide>
                );
              }
            }) : (defaultImage ? (
              <SwiperSlide className={style['swipper__slide--h-100']}>
                <Slide image={defaultImage}/>
              </SwiperSlide>
            ) : null)
          }
        </Swiper>
      </LastSliderWrapper>

    </Wrapper>
  );
};

export default PreviewSliderContainer;
